import React from 'react';
import Particles from 'react-particles-js';
import settings from './settings.json';

export default class ParticleWave extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      vpWidth: null
    };

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ vpWidth: window.innerWidth });
  }

  componentWillUnmount() {
    this.state.vpWidth;
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  render() {
    const mobileStyles = {
      position: 'absolute',
      left: '0',
      top: '-130px',
      zIndex: '0',
      willChange: 'transform',
      animation: 'wave 10s infinite alternate'
    };
    const desktopStyles = {};

    const styles = this.state.vpWidth > 1024 ? desktopStyles : mobileStyles;
    return <Particles params={settings} style={styles} />;
  }
}
